@import "../utils/_mixins";
@import "../utils/_variables";

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
    display: block;
}

html {
    height: 100%;
    -webkit-text-size-adjust: none;
    -ms-text-size-adjust: none;
}

ol,
ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

button:focus,
input:focus,
select:focus,
textarea:focus {
    outline: none;
}

input,
button,
select,
textarea {
    margin: 0;
    font-family: inherit;
    font-size: inherit;
}

input {
    box-shadow: none;
    @include radius(0);
}

a {
    text-decoration: none;
    color: inherit;
}

a:hover {
    color: inherit;
    text-decoration: none;
    @include transition(0.4s ease);
}

button {
    border: none;
    background-color: transparent;
    cursor: pointer;
}

img,
iframe {
    max-width: 100%;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}